#bg-color {
  /* background-image: url('./abc.jpg'); */
  /* background-image: url('./img-1.gif'); */
  /* background-image: url('./img-1.JPG'); */
  background-color: #2d3436;
  background-image: linear-gradient(315deg, #2d3436 0%, #000000 84%);
